'use client'

import { User } from '@supabase/supabase-js'
import { useState } from 'react'
import { useAuth } from '../context/auth'
import toast from 'react-hot-toast'

interface DashboardProps {
  user: User
}

export default function Dashboard({ user }: DashboardProps) {
  const [url, setUrl] = useState('')
  const { signOut } = useAuth()
  
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    // TODO: Implement URL submission
    toast.success('URL added successfully!')
    setUrl('')
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-2xl font-bold">Welcome, {user.email}</h1>
        <button
          onClick={() => signOut()}
          className="px-4 py-2 text-sm text-gray-600 hover:text-gray-800"
        >
          Sign Out
        </button>
      </div>

      <div className="max-w-2xl mx-auto">
        <form onSubmit={handleSubmit} className="mb-8">
          <div className="flex gap-2">
            <input
              type="url"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              placeholder="Enter a URL to share..."
              className="flex-1 rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
              required
            />
            <button
              type="submit"
              className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
            >
              Share
            </button>
          </div>
        </form>

        {/* TODO: Add shared links list */}
        <div className="space-y-4">
          <p className="text-gray-500 text-center">No shared links yet</p>
        </div>
      </div>
    </div>
  )
} 